import styled from '@emotion/styled';
import React from 'react';
import CalendarHeatmap from 'react-calendar-heatmap';
import 'react-calendar-heatmap/dist/styles.css';
import { Helmet } from 'react-helmet';
import { connect } from 'react-redux';
import ReactTooltip from 'react-tooltip';
import siteInfo from '../../config/site-info';
import { fetchUserProfile } from '../../redux/reducers/about';
import media from '../../utils/media';
import ContentLoader from '../content-loader';
import PieChart from './PieChart';

const AboutDiv = styled.div`
    display: flex;
    flex-direction: column;
    flex: 1 0 auto;
    background-color: #fff;
    .resume {
        text-align: center;
        font-size: 24px;
    }
    .about-header {
        flex: 1 0 auto;
        display: flex;
        flex-direction: row;
        border-bottom: 2px solid #444;
        .person-base {
            flex: 1 0 auto;
            .name {
                font-size: 20px;
            }
            .aim {
                font-size: 12px;
            }
            ${media.phone`
                display: none;
            `};
        }
        .person-info {
            flex: 1 0 auto;
            padding: 5px;
            list-style: none;
            li {
                font-size: 12px;
                padding: 5px;
            }
        }
        .person-img {
            flex: 1 0 auto;
            text-align: right;
            img {
                width: 90px;
                height: 90px;
                border-radius: 2px;
            }
            h3{
                display: none;
            }
            ${media.phone`
                flex: auto;
                text-align: left;
                img{
                    width: 70px;
                    height: 70px;
                }
                h3{
                    font-size: 12px;
                    display: block;
                }
            `};
        }
    }
    .about-main {
        display: flex;
        flex-direction: column;
        ${media.phone`
            padding-left: 8px;
        `};
    }
    .about-main-item {
        flex: 1 0 auto;
        .about-main-title{
            flex: 0 0 auto;
            i {
                vertical-align: text-bottom;
                margin-left: 5px;
            }
        }
        .about-main-content{
            font-size: 14px;
            line-height: 26px;
            padding: 10px 10px 0;
            margin: 0;
            list-style: circle;
            h3{
                margin:  0;
            }
            h4{
                margin: 0;
            }
            p{
                margin: 5px 0;
            }
        }
    }

    .pie-chart{
        display: flex;
        ${media.phone`
            display: block;
        `};
    }

    .skills-list {
        display: flex;
    }

    .skills-list ul {
        list-style: none;
        padding-left: 0;
        margin: 0;
        flex: 1 0 auto;
        width: 50%;
    }

    .skills-list ul li .progress {
        position: relative;
        display: block;
        width: 100%;
        height: 4px;
        background: #eee;
    }

    .skills-list ul li .progress .percentage {
        position: absolute;
        left: 0;
        top: 0;
        width: 0%;
        height: 100%;
        background: #2eca7f;
        transition: all 0.6s ease 0s;
        border-radius: 4px;
        &.html{
            background-color: #e892bb;
        }
        &.css{
            background-color: #ac97e0;
        }
        &.javascript{
            background-color: #e0b176;
        }
        &.nodejs{
            background-color: #4cf3ff;
        }
        &.mongodb{
            background-color: #709441;
        }
        &.react{
            background-color: #7fc8f8;
        }
        &.vue{
            background-color: #51b1ef;
        }
    }

    .skills-list ul li .progress {
        position: relative;
        display: block;
        width: 100%;
        height: 8px;
        background: #eee;
        border-radius: 4px;
    }

    .skills-list ul li {
        position: relative;
        padding: 15px 10px 0;
    }

    .skills-list ul li .name {
        margin: 0 0 11px 0;
        line-height: 16px;
        font-weight: 400;
    }
`;

const today = new Date();

class About extends React.Component<any, any> {

    public static asyncData(store: any) {
        return store.dispatch(fetchUserProfile('bs32g1038'));
    }

    constructor(props: any) {
        super(props);
        this.state = {
            isLoading: false
        };
    }

    public componentDidMount() {
        const { profile } = this.props._DB;
        console.log(profile);
        if (profile) {
            return;
        }
        this.setState({
            isLoading: true
        });
        About.asyncData({ dispatch: this.props.dispatch }).then(() => {
            this.setState({
                isLoading: false
            });
        });
    }

    public render() {
        const userProfile = this.props._DB.profile;
        let values = [];
        let userRepos;
        let totalContributionLastYear;
        if (userProfile) {
            userRepos = userProfile.userRepos;
            totalContributionLastYear = userProfile.userCommits && userProfile.userCommits.total || 0;
            if (userProfile && userProfile.userCommits && userProfile.userCommits.contribution) {
                values = userProfile.userCommits.contribution.map((item: any) => {
                    return {
                        year: item.year,
                        date: item.year + '-' + item.month,
                        count: item.count,
                    };
                });
            }
        }
        return (
            <AboutDiv className="about">
                {
                    this.state.isLoading ?
                        <>
                            <Helmet title={siteInfo.name + '-关于'}></Helmet>
                            <h1 className="resume">
                                <ContentLoader width={720} height={40}>
                                    <rect x="260" y="0" width="200" height="40"></rect>
                                </ContentLoader>
                            </h1>
                            <ContentLoader width={720} height={30}>
                                <rect x="0" y="0" rx="2" ry="2" width="720" height="30"></rect>
                            </ContentLoader>
                            <ContentLoader width={720} height={100}>
                                <rect x="0" y="10" rx="2" ry="2" width="160" height="100"></rect>
                                <rect x="190" y="10" rx="2" ry="2" width="160" height="100"></rect>
                                <rect x="370" y="10" rx="2" ry="2" width="160" height="100"></rect>
                                <rect x="550" y="10" rx="2" ry="2" width="160" height="100"></rect>
                            </ContentLoader>
                            <ContentLoader width={720} height={30}>
                                <rect x="0" y="20" rx="2" ry="2" width="720" height="30"></rect>
                            </ContentLoader>
                            <ContentLoader width={720} height={100}>
                                <rect x="0" y="20" rx="2" ry="2" width="720" height="100"></rect>
                            </ContentLoader>
                            <ContentLoader width={720} height={180}>
                                <rect x="0" y="20" rx="2" ry="2" width="370" height="30"></rect>
                                <rect x="0" y="70" rx="2" ry="2" width="250" height="30"></rect>
                                <rect x="0" y="120" rx="2" ry="2" width="370" height="30"></rect>
                                <circle cx="600" cy="80" r="60"></circle>
                            </ContentLoader>
                            <ContentLoader width={720} height={260}>
                                <rect x="0" y="0" width="720" height="20"></rect>
                                <rect x="40" y="40" width="460" height="30"></rect>
                                <rect x="40" y="90" width="280" height="20"></rect>
                                <rect x="40" y="130" width="280" height="20"></rect>
                                <rect x="40" y="170" width="280" height="20"></rect>
                                <rect x="40" y="210" width="280" height="20"></rect>
                            </ContentLoader>
                            <ContentLoader width={720} height={260}>
                                <rect x="0" y="0" width="720" height="20"></rect>
                                <rect x="40" y="40" width="460" height="30"></rect>
                                <rect x="40" y="90" width="280" height="20"></rect>
                                <rect x="40" y="130" width="280" height="20"></rect>
                                <rect x="40" y="170" width="280" height="20"></rect>
                                <rect x="40" y="210" width="280" height="20"></rect>
                            </ContentLoader>
                            <ContentLoader width={720} height={260}>
                                <rect x="0" y="0" width="720" height="20"></rect>
                                <rect x="40" y="40" width="460" height="30"></rect>
                                <rect x="40" y="90" width="280" height="20"></rect>
                                <rect x="40" y="130" width="280" height="20"></rect>
                                <rect x="40" y="170" width="280" height="20"></rect>
                                <rect x="40" y="210" width="280" height="20"></rect>
                            </ContentLoader>
                        </>
                        :
                        <>
                            <Helmet title={siteInfo.name + '-关于'}></Helmet>
                            <h1 className="resume">--关于--</h1>
                            <p style={{ borderLeft: '3px solid #ccc', padding: '5px 10px', backgroundColor: '#efefef' }}>
                                专注于web前端开发。喜欢新事物，关注前端动态，对新的技术有追求；涉猎广泛，喜欢 coding。
                            </p>
                            <div className="about-header">
                                <div className="person-base">
                                    <h2 className="name">冷夜流星</h2>
                                    <h3 className="aim">求职目标：web前端工程师</h3>
                                </div>
                                <ul className="person-info">
                                    <li><i className="fa fa-user fa-fw"></i>2*岁</li>
                                    <li><i className="fa fa-phone fa-fw"></i>185(*^_^*)7248</li>
                                    <li><i className="fa fa-flag fa-fw"></i>计算机科学与技术</li>
                                </ul>
                                <ul className="person-info">
                                    <li><i className="fa fa-map-marker fa-fw"></i>广东省广州市</li>
                                    <li><i className="fa fa-envelope fa-fw"></i>bs32g1038@163.com</li>
                                    <li><i className="fa fa-university fa-fw"></i>大学本科</li>
                                </ul>
                                <div className="person-img">
                                    <img src="/public/images/avatar.png" alt="头像" />
                                    <h3 className="aim">求职目标：<br />web前端工程师</h3>
                                </div>
                            </div>
                            <p> {totalContributionLastYear} contributions in the last year</p>
                            <CalendarHeatmap
                                startDate={(values[0] && values[0].year + '-1-1') || (new Date().getFullYear()) - 1 + '-1-1'}
                                endDate={today}
                                values={values}
                                classForValue={(value: any) => {
                                    if (!value) {
                                        return 'color-empty';
                                    }
                                    return `color-github-${value.count}`;
                                }}
                                tooltipDataAttrs={(value: any) => {
                                    if (!value.date) {
                                        return {};
                                    }
                                    return {
                                        'data-tip': `${value.date} has count: ${value.count}`,
                                    };
                                }}
                                showWeekdayLabels={true}
                            />
                            <ReactTooltip></ReactTooltip>
                            <div className="pie-chart">
                                <h3 style={{ minWidth: 140 }}>Stars per Repo(top 12)</h3>
                                <PieChart userRepos={userRepos}></PieChart>
                            </div>
                            <div className="about-main">
                                <div className="about-main-item">
                                    <h3 className="about-main-title" style={{ marginBottom: 0 }}>专业技能<i className="fa fa-leaf"></i></h3>
                                    <div className="about-main-content">
                                        <li>了解 photoshop 软件以及 axurepr 原型界面软件；</li>
                                        <li>能开发符合w3c规范的前台网页、对 div+css 盒子模型布局有深刻的理解; 熟悉掌握 javascript 中 dom 编程
                            基本原理、以及面向对象编程思想；</li>
                                        <li>对于JS高级里面(闭包，作用域等有一定的认识)掌握并使用前端框架 bootstrap、vue 、react 、jQuery等</li>
                                        <li>熟悉 html5 语义化标签编程和 css3 结构选择器和动画并加以使用；</li>
                                        <li>了解git操作，熟悉nodejs与npm生态化开发, docker基本使用。</li>
                                    </div>
                                    <div className="skills-list">
                                        <ul>
                                            <li className="border-line-h">
                                                <div className="name">HTML5</div>
                                                <div className="progress">
                                                    <div className="percentage html" style={{ width: '80%' }}></div>
                                                </div>
                                            </li>
                                            <li className="border-line-h">
                                                <div className="name">CSS3</div>
                                                <div className="progress">
                                                    <div className="percentage css" style={{ width: '85%' }}></div>
                                                </div>
                                            </li>
                                            <li className="border-line-h">
                                                <div className="name">Mongodb</div>
                                                <div className="progress">
                                                    <div className="percentage mongodb" style={{ width: '75%' }}></div>
                                                </div>
                                            </li>
                                            <li className="border-line-h">
                                                <div className="name">Vue/Vue-router/Vuex</div>
                                                <div className="progress">
                                                    <div className="percentage vue" style={{ width: '80%' }}></div>
                                                </div>
                                            </li>
                                        </ul>
                                        <ul>
                                            <li className="border-line-h">
                                                <div className="name">Javascript</div>
                                                <div className="progress">
                                                    <div className="percentage javascript" style={{ width: '85%' }}></div>
                                                </div>
                                            </li>
                                            <li className="border-line-h">
                                                <div className="name">NODEJS</div>
                                                <div className="progress">
                                                    <div className="percentage nodejs" style={{ width: '77%' }}></div>
                                                </div>
                                            </li>
                                            <li className="border-line-h">
                                                <div className="name">Git</div>
                                                <div className="progress">
                                                    <div className="percentage git" style={{ width: '70%' }}>
                                                    </div>
                                                </div>
                                            </li>
                                            <li className="border-line-h">
                                                <div className="name">React/React-router/Redux</div>
                                                <div className="progress">
                                                    <div className="percentage react" style={{ width: '82%' }}></div>
                                                </div>
                                            </li>
                                        </ul>
                                    </div>
                                    <div className="clear">
                                    </div>
                                </div>
                                <div className="about-main-item">
                                    <h3 className="about-main-title" style={{ marginBottom: 0 }}>项目经验<i className="fa fa-graduation-cap fa-fw"></i></h3>
                                    <ul className="about-main-content">
                                        <li>
                                            <h4>项目：博客网站开发</h4>
                                            <p>项目简介：一个类博客的应用，主要负责博客的界面ui设计，后台数据交换逻辑实现，项目文件架构，迭代开发</p>
                                            <ul className="person-info">
                                                <li>主要是使用react,nodejs等前端web技术进行网站的开发</li>
                                                <li>使用webpack构建前后端</li>
                                                <li>后端采用nodejs express mongodb。利用mongoose进行模型规划</li>
                                                <li>项目开源代码: https://github.com/bs32g1038/node-blog</li>
                                            </ul>
                                        </li>
                                        <li>
                                            <h4>项目：职业招聘系统前端开发</h4>
                                            <p>项目简介：为学校提供职业招聘信息，同时解决大学生就业问题</p>
                                            <p>主要负责该项目的前端架构以及后续的开发，在该项目中，主要使用antdesign，react，webpack等技术，和
            鸭子模型，共同构成项目的解决方案。解决了团队的分工问题以及构建了项目良好的代码结构，最终让需求变更可
以更快的被消化，极速研发、快速上线、且方便项目后期维护，降低成本，最后在产品线中开始推广。</p>
                                        </li>
                                        <li>
                                            <h4>项目：康复平台前端开发</h4>
                                            <p>项目简介：一个软硬件结合的医疗平台，主要提供快捷便利的健康医疗服务</p>
                                            <p>在项目中，主要采用了bootstrap，jq框架进行开发，针对团队繁琐重复打包html，css，js，images的工作，采用了gulp简化了操作步骤，使得开发和工作效率大大提高</p>
                                        </li>
                                        <li>
                                            <h4>项目：通信项目WEB开发</h4>
                                            <p>项目简介：茂名通信- 为用户提供宽带缴费，宽带报装，等服务的企业系统。</p>
                                            <p>个人主要负责项目的前端开发，以及部分的后端java开发，构建了通用的web后台前
            端框架，贴合了公司的业务需要，加快了产品开发。同时在另一个项目中使用了react，nodejs，antdesign
等技术，对项目的数据过滤和数据回溯等功能进行开发。</p>
                                        </li>
                                        <li>
                                            <h4>项目：在线学习webapp</h4>
                                            <p>项目简介：类腾讯课堂类型的webapp，主要是移动端开发</p>
                                            <p>在项目中，主要采用了vue框架进行开发，实现在移动端的视频播放，数据展示，海报展览，活动竞赛等功能。</p>
                                        </li>
                                    </ul>
                                </div>
                                <div className="about-main-item">
                                    <h3 className="about-main-title" style={{ marginBottom: 0, marginTop: '10px' }}>自我评价<i className="fa fa-edit fa-fw"></i></h3>
                                    <div className="about-main-content">
                                        <li>具有良好的团队精神，很强的责任感；</li>
                                        <li>工作踏实，认真负责，能够灵活处理工作中的突发事件；</li>
                                        <li>我正在找一个平台，能够充分把自己的优势发挥出来，共同努力成就一番事业。</li>
                                    </div>
                                </div>
                            </div>
                        </>
                }
            </AboutDiv>
        );
    }
}

export default connect(
    (state: any) => ({
        _DB: state.about
    })
)(About as any);